/* SafetyTools.css */

.safety-tools {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    gap: 10px; /* Spacing between buttons */
}

.safety-tools button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.3s;
}

/* Styling for the Emergency button */
.safety-tools button.emergency {
    background-color: #d9534f; /* Red color */
    color: white;
}

.safety-tools button.emergency:hover {
    background-color: #c9302c;
}

/* Styling for the Share Location button */
.safety-tools button.share-location {
    background-color: #5bc0de; /* Light blue color */
    color: white;
}

.safety-tools button.share-location:hover {
    background-color: #31b0d5;
}

/* Styling for the Report Incident button */
.safety-tools button.report-incident {
    background-color: #f0ad4e; /* Orange color */
    color: white;
}

.safety-tools button.report-incident:hover {
    background-color: #ec971f;
}