/* src/components/Alerts.css */
.alerts-container {
    position: fixed;
    top: 0;
    right: 0;
    width: 300px;
    z-index: 1000;
  }
  
  .alert {
    margin: 10px 0;
    padding: 10px;
    border-radius: 5px;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .alert.error {
    background-color: red;
  }
  
  .alert.warning {
    background-color: orange;
  }
  
  .alert button {
    border: none;
    background: none;
    color: white;
    cursor: pointer;
  }
  