/* LoginPage.css (Also suitable for SignUpPage) */
.auth-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  text-align: center;
  height: 100vh;
}

.auth-form {
  width: 100%;
  max-width: 320px; /* Control the max width for mobile devices */
}

.auth-form h2 {
  color: #333;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 20px; /* Rounded corners */
}

.auth-button {
  width: 100%;
  padding: 10px;
  background-color: #34b538; /* Green background */
  color: white;
  border: none;
  border-radius: 20px; /* Rounded corners */
  cursor: pointer;
}

.auth-link {
  margin-top: 20px;
}

.auth-link span {
  color: #2362c8;
  cursor: pointer;
}

/* Responsive styles */
@media only screen and (max-width: 600px) {
  .auth-page {
    padding: 10px;
    height: auto;
  }

  .auth-form {
    width: 90%; /* Take up more width on smaller screens */
  }
}

/* For iPhone 6/7/8/X */
@media only screen and (min-width: 375px) and (max-width: 812px) {
  .auth-form {
    max-width: 340px;
  }
}