/* ChatInterface.css */
.chat-interface {
  display: flex;
  flex-direction: column;
  max-width: 500px; /* or your preferred width */
  margin: auto;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
}

.messages-container {
  height: 300px; /* or your preferred height */
  overflow-y: auto;
  padding: 10px;
  background-color: #f9f9f9;
  border-bottom: 1px solid #eee;
}

.message {
  margin-bottom: 10px;
  padding: 5px;
  border-radius: 5px;
  background-color: #e7f5ff;
  align-self: flex-start;
}

.message-sender {
  font-weight: bold;
  margin-right: 6px;
}

.message-timestamp {
  display: block;
  font-size: 0.7rem;
  color: #888;
  margin-top: 5px;
}

.send-message-form {
  display: flex;
  padding: 10px;
  background-color: #fff;
}

.message-input {
  flex-grow: 1;
  padding: 8px;
  margin-right: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.send-button {
  padding: 8px 16px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.send-button:hover {
  background-color: #0056b3;
}