/* BottomNav.css */

.bottom-nav {
    display: flex;
    justify-content: space-around;
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #333;
    color: white;
  }
  
  .bottom-nav-item {
    text-decoration: none;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .bottom-nav-item span {
    margin-top: 0.5rem;
  }