/* LocationPage.css */

.location-page {
  padding: 20px;
  background-color: #f4f4f4; /* Light grey background */
}

/* Style for the page title */
.location-page h1 {
  text-align: center;
  margin-bottom: 20px;
}

/* Styling for the map container */
.map-view {
  width: 100%;
  height: 400px; /* Adjust height as needed */
  margin-bottom: 20px;
}

/* List of contacts */
.location-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Individual list items */
.location-list-item {
  background-color: #ffffff; /* White background */
  border: 1px solid #dddddd; /* Light grey border */
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px; /* Rounded corners */
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Style for buttons in list items */
.location-list-item button {
  padding: 5px 10px;
  background-color: #007bff; /* Blue background */
  color: white;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.location-list-item button:hover {
  background-color: #0056b3; /* Darker blue on hover */
}

/* Responsive styling */
@media (max-width: 600px) {
  .location-page {
    padding: 10px;
  }
  .map-view {
    height: 300px; /* Smaller height for smaller screens */
  }
}
